.works-section {
    display:flex;
    flex-direction: column;
    overflow: hidden;
}

.background-box {
    z-index: 1;
    position: absolute;
    height: 110vh;
    width: 100vw;
    background-color: #AAF683;
    /* top: 10%; */
    margin-top: 22%;
}

.box1 {
    /* height: 45vh;
    width: 75vw; */
    border-top: #FF9B85 solid 10px;
    border-right: #FF9B85 solid 10px;
    border-bottom: #FF9B85 solid 10px;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    background-color: #F3F4E2;
    margin-bottom: 5%;
    z-index: 5;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    padding: 1%;
    display: flex;
    /* flex-direction: row-reverse; */
    font-size: 2.5rem;
    font-weight: 300;
    color: #338457;
}
